.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 100px;
    width: 700px;
    margin-bottom: 100px;
    padding-bottom: 20px;
    border-radius: 10px;
    box-shadow: 0 0 10px 5px #99999920;
    background-color: white;
}

.signup-container {
    width: 700px;
}

.profile-container {
    position: relative;
}

.profile-picture-container {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
}

.signup-block {
    margin: 20px 0;
}

.sign-up-button {
    grid-column: 1/4;
    justify-self: center;
}

.heading {
    font-size: 24px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: #424242;
}

.grid-container .form-field{
    margin-left: 30px;
}

.link {
    color: blue;
    text-decoration: underline;
    cursor: pointer;
}

.user-profile-picture {
    grid-area: "profile";
}

.username {
    grid-column: 1/3;
    grid-row:1;
}

.address {
    grid-column: 1/3;
    grid-row: 4;
}

.user-info {
    grid-area: "user-info";
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 40px;
}

.user-info label {
    font-size: 12px;
}

.logout {
    position: absolute;
    right: 30px;
    top: 30px;
}
.edit {
    position: absolute;
    right: 100px;
    top: 30px;
}
.user-profile {
    display:grid;
    grid-template-areas: 
        "profile user-info user-info user-info";
    justify-content: center;
    column-gap: 40px;
    width: 550px;
}

@media(max-width: 500px) {
    .container {
        width:  100%;
    }
}


.form-field {
    margin-top: 20px;
    margin-bottom: 20px;
}

.signup-form {
    justify-content: center;
    display: grid;
    grid-template-columns: 1fr 2fr;
    width: 100%;
}

.login-form {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.form-fields {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 20px;
}

.alert {
    border: 1px solid #FF0000;
    border-radius: 5px;
    background-color: #FF000020;
    color: #FF0000;
    padding: 10px 10px;
}